  --color-primary: #121212;
  --color-primary-lighter: color-mod(var(--color-primary) tint(20%));
  --color-text: color-mod(var(--color-text-bold) l(30%));
  --color-text-secondary: #757575;
  --color-text-bold: #222;
  --color-accent: #f83435;
  --color-bg-secondary: #e7e7e7;
  --color-bg-primary: #ffffff;
  --color-bg: #f5f6f7;
  /* Fonts */
  --font-main: Georgia, Iowan Old Style, Droid Serif, Times;
  --font-secondary: Kelson Sans, "Segoe UI", Roboto Condensed, Helvetica, Arial, sans-serif;
  --font-mono:  SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --font-normal: 400;
  --font-bold: 700; }

/* Color scheme: Dark */
:root[color-mode="dark"] {
  --color-primary: #f1f1f1;
  --color-primary-lighter: #adadad;
  --color-text: #dadada;
  --color-text-secondary: #dadada;
