.burger {
  position: relative;
  cursor: pointer; }
  .burger__box {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 33px;
    height: 33px; }
    .burger__box::before {
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      bottom: 0;
      margin: auto;
      content: '';
      width: 100%;
      height: 3px;
      background: currentcolor;
      transition: transform 300ms cubic-bezier(0.2, 0.6, 0.3, 1), width 300ms cubic-bezier(0.2, 0.6, 0.3, 1);
      will-change: transform, width; }
  .burger__inner {
    width: 100%;
    height: 100%; }
    .burger__inner::before, .burger__inner::after {
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      bottom: 0;
      margin: auto;
      content: '';
      width: 100%;
      height: 3px;
      background: currentcolor;
      transition: transform 250ms cubic-bezier(0.2, 0.7, 0.3, 1), width 250ms cubic-bezier(0.2, 0.7, 0.3, 1);
      will-change: transform, width; }
    .burger__inner::before {
      transform: translatey(-6px); }
    .burger__inner::after {
      transform: translatey(6px); }

